var _self,
	page = 1,
	timer = null;
export default {
	data() {
		return {
			TabCur: 1,
			scrollLeft: 0,
			tab: [{
				name: '进行中',
				value: '1'
			}, {
				name: '已结束',
				value: '2'
			}, {
				name: '全部',
				value: '3'
			}],
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			newsList: [],
			loadingText: '加载中...',
			loadingType: 0,

		}
	},
	onLoad: function() {
		_self = this;
		//页面一加载时请求一次数据
		this.getData();
	},
	onReachBottom: function() {
		//触底的时候请求数据，即为上拉加载更多
		//为了更加清楚的看到效果，添加了定时器
		console.log('触底了~~~~~~~~~~~~~~~')
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_self.getData();
		}, 1000);
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		getData() {
			if (_self.loadingType !== 0) { //loadingType!=0;直接返回
				return false;
			}
			_self.loadingType = 1;
			uni.showNavigationBarLoading(); //显示加载动画
			// uni.request({
			// 	url: 'https://demo.hcoder.net/index.php?user=hcoder&pwd=hcoder&m=list1&page=' + page,
			// 	method: 'GET',
			// 	success: function(res) {
					
					// if (res.data == null) { //没有数据
					// 	_self.loadingType = 2;
					// 	uni.hideNavigationBarLoading(); //关闭加载动画
					// 	return;
					// }
					page++; //每触底一次 page +1
					let data=[{name:'183',id:123}]
					_self.newsList = _self.newsList.concat(data); //将数据拼接在一起
					_self.loadingType = 0; //将loadingType归0重置
					uni.hideNavigationBarLoading(); //关闭加载动画
			// 	}
			// });
		},
		getnewsList: function() { //第一次回去数据
			page = 1;
			this.loadingType = 0;
			uni.showNavigationBarLoading();
			// uni.request({
			// 	url: 'https://demo.hcoder.net/index.php?user=hcoder&pwd=hcoder&m=list1&page=1',
			// 	method: 'GET',
			// 	success: function(res) {
					page++; //得到数据之后page+1
					_self.newsList = [{name:'123',id:123}];
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh(); //得到数据后停止下拉刷新
				// }
			// });
		}
	}
}
